import React from 'react';

class Header extends React.Component {
    constructor(props) {
        super(props);
    }
    
    render() {
        if(this.props.data){
            var name = this.props.data.name;
            var grade = this.props.data.grade;
            var school = this.props.data.school;
            var occupation = this.props.data.occupation;
            var description = this.props.data.description;
            var city = this.props.data.city;
            var networks = this.props.data.social.map(function(network){
                return <li key={network.name}><a href={network.url}><i className={network.className}></i></a></li>
            })
        }

        return (
            <header id="home" style={{backgroundColor: 'white'}}>
            <nav id="nav-wrap">
                <a className="mobile-btn" href="#nav-wrap" title="Show navigation">Show navigation</a>
	            <a className="mobile-btn" href="#home" title="Hide navigation">Hide navigation</a>

                <div style={{backgroundColor:'#779ecb'}}>
                <ul id="nav" className="nav">
                    <li className="current"><a href="#home">Home</a></li>
                    <li><a href="#resume">Resume</a></li>
                    <li><a href="#portfolio">Projects</a></li>
                    <li><a href="#about">About</a></li>
                </ul>
                </div>
            </nav>
            <div className="row banner">
                <div className="banner-text">
                    <h1 className="responsive-headline">I'm {name}.</h1>
                    <h3>I'm a <span>{occupation}</span> at <span>{school}</span>. {description}.</h3>
                    <hr />
                    <ul className="social">
                        {networks}
                    </ul>
                </div>
            </div>
            <p className="scrolldown">
                <a href="#resume"><i className="icon-down-circle"></i></a>
            </p>
            </header>
        )
    }
}

export default Header;